.skillBlock {
    min-width: 100px;
    max-width: 300px;
    background: #02020e;
    text-align: center;
    padding: 10px;
    border-radius: 7px;
    margin: 10px;

    display: flex;
    flex-direction: column;
    align-items: center;
}

.icon {
    width: 100px;
    height: 100px;
    margin-top: 10px;
}

.icon img {
    width: 100%;
}

.skillBlock h3 {
    margin: 10px;
}

.description {
    text-align: justify;
    text-indent: 15px;
}